
body{margin: 0; padding:0;
     -webkit-font-smoothing: antialiased;
	   -moz-osx-font-smoothing: grayscale;
}
  /* body{background: url("./assets/background-temp.jpg") no-repeat center center fixed; background-size: cover; } */
  /* img{display: none; width:135px; position: absolute; margin: auto auto; left:0; right:0; top:0; bottom:0; } */
* {font-family: "proxima-nova";
}

* {
  -webkit-transition: all 300ms;
  transition: all 300ms;
}
.row .logo {
  justify-self: center;
}
.row .logo img {
    width: 135px;
    display: none;
}
.row .top-info {
  background: #73C1FF;
}
.row .top-info .content {
    max-width: 650px;
    padding: 20px 40px;
    font-style: italic;
}
.row .top-info .title {
    font-size: 30px;
    color: white;
    font-family: "garamond-premier-pro-display";
    letter-spacing: 0.02em;
    border-bottom: solid 1px #4E86B0;
    display: inline-block;
    font-style: normal;
    font-weight: 400;
    padding-bottom: 20px;
}
.row .top-info .sub {
    font-size: 16px;
    color: #1C2E3D;
    font-weight: 500;
    margin-top: 15px;
}
.row .top-info .sub span {
      color: white;
      font-style: italic;
}
.row .top-info .blurb {
    font-size: 18px;
    color: #385F7D;
    line-height: 1.4;
    font-weight: 500;
    margin-top: 35px;
}
.row .top-info .more {
    font-size: 16px;
    font-style: normal;
    color: #385F7D;
    font-weight: 600;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin-top: 20px;
    cursor: pointer;
    padding: 5px 0;
}
.row .top-info .more .cross {
      width: 13px;
      padding-right: 15px;
      margin-top: 1px;
}
.row .top-info .more .cross img {
        -webkit-transition: -webkit-transform 300ms;
        transition: -webkit-transform 300ms;
        transition: transform 300ms;
        transition: transform 300ms, -webkit-transform 300ms;
        -webkit-transform-origin: center;
                transform-origin: center;
}
.row .top-info .more .cross.close img {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
}
.row .top-info .more:hover {
      color: white;
}
.row .number {
  display: none;
}
.row .question {
  background: #E6E7E8;
  padding: 20px 0;
}
.row .question .info {
    padding: 0 40px;
}
.row .question .info .txt {
      font-size: 20px;
      color: #3B3B38;
      margin-bottom: 10px;
      padding-bottom: 10px;
      border-bottom: solid 1px #BBBBBB;
}
.row .answers {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.row .lexi {
  margin-top: 8px;
}
.row .lexi .label {
    font-size: 15px;
    color: #777878;
    font-weight: 400;
    padding-left: 6px;
}
.row .submit {
  height: 55px;
  background: #73C1FF;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 0 40px;
  cursor: pointer;
  color: white;
  font-weight: bold;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}
.row .submit:hover {
    background: #3698E6;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
form {
  display: none;
}
@media screen and (min-width: 800px) {
.row {
    display: grid;
    grid-template-columns: 240px auto;
}
.row .logo {
      padding-top: 50px;
}
.row .logo img {
        display: initial;
}
.row .number {
      display: block;
      font-size: 21px;
      color: #777878;
      font-style: italic;
      justify-self: end;
      padding: 45px 70px 0 0;
}
.row .question {
      padding: 40px 0;
}
.row .question .info {
        padding: 0 0 0 70px;
        max-width: 680px;
}
.row .question .info .txt {
          font-size: 27px;
}
.row .lexi {
      margin-top: 8px;
}
.row .lexi .label {
        font-size: 21px;
        padding-left: 15px;
}
.row .top-info {
      padding-bottom: 60px;
}
.row .top-info .content {
        max-width: 650px;
        padding: 50px 0 0 70px;
}
.row .top-info .title {
        font-size: 60px;
        padding-bottom: 20px;
}
.row .top-info .sub {
        font-size: 26px;
}
.row .top-info .blurb {
        font-size: 25px;
}
}
